/*==========  Mobile First Method  ==========*/

@media only screen and (min-width : 0px) and (max-width: 767px) {

  .content {
    &__head {
      &--top {
        padding: 0;
        margin: 0 auto;
        width: 90%;
        .h1, h1 {
          padding: 0;
          margin: 0; }
        &::after {
          display: none; } } }
    &__top {
      .container {
        padding-left: 0;
        padding-right: 0; } } }

  .navigation {
    position: absolute;
    z-index: 200;
    width: 270px;
    left: 50%;
    margin-left: -135px;
    background-color: $color-red;
    padding: 5px;
    top: 155px;
    display: none;
    opacity: 0;
    animation: open 0.5s ease-in-out forwards;
    &::before {
      content: '';
      position: absolute;
      top: -11px;
      left: 50%;
      transform: translateX(-50%);
      width: 0;
      height: 0;
      border-left: 11px solid transparent;
      border-right: 11px solid transparent;
      border-bottom: 11px solid $color-red; }
    &>ul {
      border: 1px solid rgba($color-white, 0.5);
      padding: 10px 0;
      &>li {
        &>a, &>span {
          padding: 0 25px;
          height: 34px;
          line-height: 34px;
          &:hover {
            color: $color-darkgray; } }
        &.active {
          &>a, &>span {
            color: $color-darkgray; } } } }
    &__row {
      &.open {
        .navigation {
          display: block;
          opacity: 1; }
        .btn--mobile {
          color: $color-red;
          strong {
            color: $color-red; } } } } }

  .btn {
    &--mobile {
      display: block; } }

  .contacts {
    &__info {
      &__body {
        &>div {
          &:not(:last-child) {
            .contacts__block {
              margin-bottom: 20px; } } } } } } }

@media only screen and (min-width : 0px) and (max-width: 991px) {
  .autopark {
    &__bottom {
      &>.container {
        width: auto; } }
    &__block {
      &>div {
        max-width: 500px;
        margin: 0 auto; } } } }

