  &__select {
    background-color: #f2f2f2;
    position: relative;
    z-index: 100;
    margin-bottom: -20px;
    &__mobile {
      padding: 25px 0;
      display: block;
      position: relative; }
    &__desctop {
      display: none;
      padding: 25px 0;
      position: relative;
      &__body {
        display: table;
        height: 100px;
        width: 100%;
        &>div {
          display: table-cell;
          vertical-align: middle;
          &:first-child {
            border-right: 1px solid $color-white;
            width: 90px;
            text-align: center; } }
        ul {
          margin: 0;
          padding: 0;
          display: table;
          width: 100%;
          &>li {
            margin: 0;
            list-style: none;
            display: table-cell;
            vertical-align: middle;
            width: calc(100% / 7);
            &>a {
              display: block;
              height: 100px;
              text-align: center;
              text-decoration: none;
              font-family: $font-sportword;
              text-transform: uppercase;
              padding-top: 37px;
              &:hover {
                svg {
                  path {
                    fill: $color-red; } } }
              small {
                font-size: rem(12);
                opacity: 0; }
              svg {
                display: block;
                margin: 0 auto 5px !important;
                height: 25px;
                path {
                  transition: color 0.3s ease;
                  fill: #cfcfcf; } } }
            &.active {
              &>a {
                small {
                  opacity: 1; }
                svg {
                  path {
                    fill: $color-red; } } } }

            &:not(:last-child) {
              &>a {
                border-right: 1px solid $color-white; } } } } } }

    &__top {
      height: 100px;
      display: table;
      width: 100%;
      &>div {
        display: table-cell;
        vertical-align: middle;
        padding: 0 15px;
        &:first-child {
          border-right: 1px solid $color-white;
          width: 90px;
          text-align: center; }
        &:last-child {
          width: 70px;
          text-align: center; } }
      .span {
        color: #cfcfcf;
        &--car {
          display: none !important; } }
      svg {
        path {
          transition: color 0.3s ease;
          fill: #cfcfcf; } } }

    &__bottom {
      position: absolute;
      width: 100%;
      background-color: #f2f2f2;
      border-top: 1px solid $color-white;
      padding: 10px 0;
      top: 150px;
      opacity: 0;
      display: none;
      animation: menuDropdown 0.5s ease;
      &.active {
        display: block;
        opacity: 1; }
      &>ul {
        margin: 0;
        padding: 0;
        &>li {
          margin: 0;
          list-style: none;
          &>a {
            display: block;
            color: #cfcfcf;
            text-decoration: none;
            padding: 10px 0px 10px 20px; }
          &:hover {
            &>a {
              color: $color-red !important;
              svg {
                path {
                  fill: $color-red !important; } } } }
          &.selected {
            &>a {
              color: $color-red !important;
              background-color: #dcdddd;
              .ion-android-checkbox-outline-blank {
                display: none; }
              .ion-android-checkbox-outline {
                display: block; }
              svg {
                path {
                  fill: $color-red !important; } } } } } } }

    &__checkbox {
      i {
        font-size: rem(40);
        display: none;
        line-height: 0;
        &.ion-android-checkbox-outline-blank {
          display: inline-block; } }

      svg {
        path {
          transition: color 0.3s ease;
          fill: #cfcfcf; } } }

    .span {
      display: inline-block;
      vertical-align: middle;
      &--checkbox {
        margin-right: 15px; }
      &--car {
        width: 70px;
        text-align: center;
        margin-right: 15px; }
      &--info {
        font-size: rem(10);
        font-family: $font-sportword;
        text-transform: uppercase;
        small {
          display: block;
          line-height: 1;
          font-size: rem(10); }
        strong {
          font-size: rem(27);
          display: block;
          line-height: 1;
          font-family: $font-sportword;
          margin-top: -3px; } } }

    &__filter {
      font-size: rem(46);
      color: #444444;
      text-decoration: none;
      &.active {
        color: $color-red; } }

    &__all-filter {
      font-size: rem(10);
      font-family: $font-sportword;
      text-transform: uppercase;
      text-decoration: none;
      display: block;
      small {
        display: block;
        line-height: 1;
        font-size: rem(10); }
      strong {
        font-size: rem(27);
        display: block;
        line-height: 1;
        font-family: $font-sportword;
        margin-top: -3px; } } } }

@keyframes menuDropdown {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

