/**/
.car-list {
  @include clearfix;
  &>div {
    width: 100%;
    float: none; }

  &__auto {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 100%;
    max-height: 175px; }

  &__human {
    position: absolute;
    // display: none
    left: 15px;
    bottom: 0;
    max-height: 80%;
    transform: translateY(15px);
    opacity: 0;
    transition: transform 1s ease, opacity 1s ease;
    will-change: transform; }

  &__smoke {
    position: absolute;
    bottom: 135px;
    left: 95px;
    width: 127px;
    // background-color: rgba(pink,0.2)
    opacity: 0;
    transform: translateY(-2px);
    transition: transform 1s ease, opacity 1s ease;
    will-change: transform; }

  &__auto {
    position: absolute;
    max-width: 100%;
    height: auto;
    filter: grayscale(100%);
    transition: filter 0.7s ease-in-out; }

  &__block {
    width: 100%;
    height: 240px;
    position: relative;
    transition: background 0.3s linear;
    overflow: hidden;
    background: {
      color: $color-darkgray;
      position: top center;
      repeat: no-repeat;
      size: cover;
      image: url('../img/car-list/car-list-background.jpg'); }
    &:hover {
      background: {
        image: url('../img/car-list/car-list-background-hover.jpg'); }
      .car-list__auto {
        filter: grayscale(0%); }
      .car-list__human {
        // animation: human 1s ease-in forwards normal
        transform: translateY(0px);
        opacity: 1; }
      .car-list__smoke {
        opacity: 1;
        transition-delay: 1s;
        transform: translateY(0px); }
      .car-list__title__bottom {
        transform: translateY(0%);
        transition-delay: 0.5s; } } }

  &__title {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 50;

    &__top {
      width: 230px;
      height: 52px;
      color: $color-white;
      background-color: $color-red;
      text-align: center;
      font-size: rem(18);
      font-family: $font-sportword;
      padding-top: 10px;
      position: relative;
      z-index: 20;
      a {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 10; }
      strong {
        display: block;
        font-family: $font-sportword;
        line-height: 1;
        margin-bottom: 3px; }
      span {
        font-size: rem(10);
        display: block;
        line-height: 1; }
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: -23px;
        width: 0;
        height: 100%;
        @include triangle(23px 52px, $color-red, up-right); } }

    &__bottom {
      // display: none
      background-color: $color-red;
      color: $color-white;
      position: absolute;
      width: 100%;
      // padding: 35px 25px
      padding: 15px 15px;
      font-size: rem(14);
      // font-size: rem(16)
      font-family: $font-dejavu-cond-book;
      will-change: transform;
      transform: translateY(-200%);
      transition: transform 0.5s ease-out;
      z-index: 10;
      &::after {
        content: '';
        position: absolute;
        // bottom: -46px
        bottom: -26px;
        left: 0;
        width: 0;
        // @include triangle(230px 46px, $color-red, up-right)
        @include triangle(230px 26px, $color-red, up-right); }
      ul {
        margin: 0;
        padding: 0;
        &>li {
          list-style: none;
          margin: 0;
          &:not(:first-child) {
            // padding-top: 5px
            padding-top: 2px; } } } } } }

