/* buttons */

.btn {
  display: inline-block;
  padding: 6px 12px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  user-select: none;
  background: {
    image: none;
    color: $color-gray; }
  border: 1px solid transparent;
  border-radius: 4px;

  &--default {
    color: #333;
    background-color: $color-white;
    border-color: $color-gray; }

  &--mobile {
    background-color: transparent;
    padding: 0;
    margin: 0;
    border-radius: 0;
    color: $color-white;
    line-height: 1;
    border: 0;
    display: block;
    position: absolute;
    top: 120px;
    left: 50%;
    transform: translateX(-50%);
    transition: color 0.5s ease-in-out;
    display: none;

    strong {
      font-size: rem(19);
      text-transform: uppercase;
      font-family: $font-sportword;
      color: #e6e9ec;
      transition: color 0.5s ease-in-out; }

    .ion-navicon-round {
      margin-right: 10px;
      font-size: rem(21);
      position: relative;
      top: 1px; } }

  &--right-small {
    @include sprite($btn-right-small);
    line-height: 24px;
    border: 0;
    background-color: transparent;
    color: $color-white;
    font-size: rem(11);
    text-transform: uppercase;
    font-family: $font-sportword;
    text-decoration: none;
    // transition: all 0.1s ease
    &:hover, &:active, &:focus {
      @include sprite($btn-right-small-hover);
      color: $color-white; } }

  &--contacts {
    display: block;
    margin: 0 auto; }

  &--prices {
    @extend .btn--right-small;
    @include sprite($btn-left-small);
    &:hover {
      @include sprite($btn-left-small-hover); } }

  &--big-order {
    @extend .btn--right-small;
    @include sprite($btn-right-big);
    font-size: rem(21);
    &:hover {
      @include sprite($btn-right-big-hover); } }

  &--more {
    @extend .btn--right-small;
    @include sprite($btn-left-small);
    &:hover {
      @include sprite($btn-left-small-hover); } }

  &--fast-order {
    padding: 0;
    height: auto;
    line-height: inherit;
    font-family: $font-sportword;
    font-size: rem(11);
    text-transform: uppercase;
    background-color: transparent;
    text-decoration: none;
    width: 145px;
    text-align: center;
    border: 0;
    &:hover {
      text-decoration: underline; } }

  &--popup__order {
    @extend .btn--right-small;
    &:hover {
      @include sprite($btn-right-small-hover); } } }
